import { Group, SpotLight, AmbientLight, HemisphereLight } from 'three';

class BasicLights extends Group {
    constructor(...args) {
        // Invoke parent Group() constructor with our args
        super(...args);

        const dir = new SpotLight(0xFF80BF, 1, 7, 0.8, 1, 1);
        const ambi = new AmbientLight(0xffbf80, 1);
        const hemi = new HemisphereLight(0x66ccff, 0x800000, 1);

        dir.position.set(5, 1, 2);
        dir.target.position.set(0, 0, 0);

        this.add(ambi, hemi, dir);
    }
}

export default BasicLights;
